import { useState } from "react";
import Head from "../../blog/Head";
import PageInfo from "../../blog/posts/2022-06-23-wd2uk";

export default function Wordl() {
  const [IO, setIO] = useState(false);
  const [fail, setFail] = useState(true);
  const [output_result, setOutput] = useState("");
  const [state_msg, setMsg] = useState("");
  let result = "";

  const printUK = () => {
    setIO(true);
    const wordl = document.querySelector("#wd_input").value;
    if (wordl.length !== 0) {
      let wordlFlag = false; // Wordle  
      let type = 0b00; // type  bitmask

      for (let i = 0; i < wordl.length; i++) {
        //    ()
        if (wordl[i] === "\uD83D") {
          switch (wordl[i + 1]) {
            // wrong spot
            case "\uDFE6": // 
            case "\uDFE8": // 
              result += "";
              wordlFlag = true;
              break;
            // correct spot
            case "\uDFE7": // 
            case "\uDFE9": // 
              result += "";
              wordlFlag = true;
              break;
            default:
              result += wordl[i];
              continue;
          }

          // type check
          switch (wordl[i + 1]) {
            case "\uDFE6": // 
            case "\uDFE7": // 
              type |= 0b10;
              break;
            case "\uDFE8": // 
            case "\uDFE9": // 
              type |= 0b01;
              break;
            default:
              result += wordl[i];
              continue;
          }
          //  2 .
          i++;
        }
        // not in any spot
        //      .
        else if (wordl[i] === "" || wordl[i] === "") {
          result += "";
          wordlFlag = true;
        }
        // plain character
        else result += wordl[i];
      }

      if (wordlFlag && type !== 0b11) {
        setFail(false);
        setOutput(result);
      } else {
        setOutput("Wordle     :(\n");
      }
    } else {
      setOutput("  :(");
    }
  };

  const retryUK = () => {
    setIO(false);
    setFail(true);
    setMsg("");
  };

  const shareUK = () => {
    // if (navigator.share) {
    //   navigator
    //     .share({
    //       text: output_result,
    //     })
    //     .then(() => setMsg("  "))
    //     .catch((err) => setMsg("  "));
    // } else {
    navigator.clipboard.writeText(output_result);
    setMsg("   ");
    // }
  };

  const InputRes = () => (
    <>
      <h1>
        Wordle 
        <br />
         
        <br />
        <img
          src="/res/wd2uk/favicon.ico"
          alt="UK flag"
          width={32}
          className="inline"
        />{" "}
         
      </h1>
      <textarea
        autoFocus
        required
        id="wd_input"
        rows="8"
        cols="20"
        placeholder="Wordle  &#10; ."
        style={{
          padding: 5,
          borderRadius: 5,
          backgroundColor: "#44475A",
          color: "#F8F8F2",
          fontFamily: "'IBM Plex Sans KR', 'Bahnschrift'",
        }}
        className="mt-[10px]"
      />
      <br />
      <button
        style={{
          backgroundColor: "#50FA7B",
          border: "solid 2px #50FA7B",
          borderRadius: 5,
          fontFamily: "'IBM Plex Sans KR', 'Bahnschrift'",
        }}
        onClick={printUK}
        className="px-[2px] text-black"
      >
        
      </button>
    </>
  );

  const OutputRes = () => (
    <>
      <div>{output_result}</div>
      <br />
      {state_msg.length !== 0 ? (
        <>
          <div>{state_msg}</div>
          <br />
        </>
      ) : null}
      {!fail ? (
        <>
          <input
            type="button"
            style={{
              backgroundColor: "#50FA7B",
              border: "solid 2px #50FA7B",
              borderRadius: 5,
              fontFamily: "'IBM Plex Sans KR', 'Bahnschrift'",
            }}
            onClick={shareUK}
            value=""
          />
          &emsp;
        </>
      ) : null}
      <input
        type="button"
        style={{
          backgroundColor: "#FF79C6",
          border: "solid 2px #FF79C6",
          borderRadius: 5,
          fontFamily: "'IBM Plex Sans KR', 'Bahnschrift'",
        }}
        onClick={retryUK}
        value=""
      />
    </>
  );

  return (
    <>
      <Head page={PageInfo} />
      <div className="App">{!IO ? <InputRes /> : <OutputRes />}</div>
    </>
  );
}
